import { useParams } from 'react-router-dom'
import { useQuery, gql } from '@apollo/client'
import { Container, Row, Col, Accordion } from 'react-bootstrap'
import CityTranslators from './CityTranslators'

import QueryResult from './query-result'

const COUNTRY_TRANSLATORS = gql`
  query CountryTranslators ($isoCode: String) {
    countryTranslators(isoCode: $isoCode) {
      id
      name
      isoCode
      Cities {
        id
        name
        region
        Translators {
          firstName
          lastName
          organisation
          streetAddress
          postalCode
          email
          phone
          website
          languages
        }
      }
    }
  }
`

const numCities = data => data?.countryTranslators?.Cities.length || 0
const numTranslators = data => data
  ? data?.countryTranslators.Cities.reduce((acc, cur) => acc + cur.Translators.length, 0)
  : 0
const countryName = data => data?.countryTranslators.name || ''

const CountryTranslators = () => {
  const { isoCode } = useParams()
  const { loading, error, data } = useQuery(COUNTRY_TRANSLATORS, { variables: { isoCode } })
  return (
    <QueryResult loading={loading} error={error} data={data}>
      <Container>
        <Row>
          <Col className='h5'>
            {countryName(data)} - {numCities(data)} cities - {numTranslators(data)} translators
          </Col>
        </Row>
        <Row>
          <Col>
            <Accordion defaultActiveKey={0}>
              {data?.countryTranslators.Cities.map((city, key) =>
                <CityTranslators
                  country={countryName(data)}
                  city={city}
                  key={key}
                  index={key}
                />)}
            </Accordion>
          </Col>
        </Row>
      </Container>
    </QueryResult>
    // <Container>
    //   <Row>
    //     <Col className='h4'>
    //       {translatorData?.data?.listCountries?.items[0]?.name || null}
    //       {
    //         translatorsCount === 0
    //           ? <Badge pill bg='info'>0</Badge>
    //           : <Badge pill bg='success'>{translatorsCount}</Badge>
    //       }

  //     </Col>
  //   </Row>
  //   <Row>
  //     <Accordion defaultActiveKey='0'>
  //       {
  //       translatorData?.data?.listCountries?.items[0]?.Cities?.items.length > 0
  //         ? sortBy(prop('name'))(translatorData?.data?.listCountries?.items[0]?.Cities.items).map((city, key) => {
  //             return (
  //               <CityTranslators
  //                 index={key}
  //                 country={translatorData.data.listCountries.items[0].name}
  //                 city={city}
  //                 key={key}
  //               />
  //             )
  //           })
  //         : null
  //     }
  //     </Accordion>
  //   </Row>
  // </Container>
  )
}

export default CountryTranslators
