import { Badge, Accordion } from 'react-bootstrap'
import Translator from './Translator'

const CityTranslators = ({ city, country, index }) => {
  return (
    <Accordion.Item eventKey={index}>
      <Accordion.Header>
        {city.name}
        &nbsp;
        <Badge>{city?.Translators?.length}</Badge>
      </Accordion.Header>
      <Accordion.Body>
        {
        city?.Translators?.length > 0
          ? city.Translators.map((translator, key) => {
              return <Translator translator={translator} country={country} city={city.name} key={key} />
            })
          : null
      }
      </Accordion.Body>
    </Accordion.Item>
  )
}

export default CityTranslators
