import React from 'react'
import { Button } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'

const CountryCard = ({ country }) => {
  const { id, name, isoCode } = country
  const navigate = useNavigate()
  return (
    <Button
      key={id}
      className='m-1 btn btn-outline-dark'
      variant='outline-secondary'
      onClick={() => { navigate(`/country/${isoCode}`) }}
    >
      <div className='d-flex flex-row'>
        <img alt='flag' className='m-2' variant='top' src={`https://flagcdn.com/${isoCode}.svg`} style={{ height: '4em', width: '6em', marginTop: '1em', border: '1px solid grey' }} />
        <div className='d-flex flex-column justify-content-center'>
          {name}
        </div>
      </div>
    </Button>
  )
}

export default CountryCard
