import { Card, Badge, ListGroup } from 'react-bootstrap'

const Translator = ({ translator, country, city }) => {
  console.log('first', translator)
  const {
    firstName,
    lastName,
    organisation,
    streetAddress,
    postalCode,
    phone,
    email,
    website,
    languages
  } = translator
  const phones = phone !== null ? phone.split(',') : null
  const emails = email !== null ? email.split(',') : null
  const langs = languages !== null ? languages.split(',') : [null]

  return (
    <Card className='my-1'>
      {(firstName || lastName) && <Card.Header>{firstName} {lastName}</Card.Header>}
      {organisation && <Card.Header>{organisation}</Card.Header>}
      <Card.Body>
        <Card.Text>{streetAddress} {postalCode} {city} {country}</Card.Text>
      </Card.Body>
      <ListGroup variant='flush'>
        {
          phones !== null && phones.map((p, key) => <ListGroup.Item key={key}><Card.Link href={`tel:${p}`}>{p}</Card.Link></ListGroup.Item>)
        }
        {
          emails !== null && emails.map((p, key) => <ListGroup.Item key={key}><Card.Link href={`mailto:${p}`}>{p}</Card.Link></ListGroup.Item>)
        }
        {website && <ListGroup.Item><Card.Link href={website}>{website}</Card.Link></ListGroup.Item>}
        <ListGroup.Item>
          {
            langs.map((p, key) => <Badge className='mx-1' key={key} pill bg='info'>{p}</Badge>)
          }
        </ListGroup.Item>

      </ListGroup>
    </Card>
  )
}

export default Translator
